/*BENEFIT BENEFITS*/

.benefits {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: $color-light-grey;
  padding-top: 105px;
  padding-bottom: 105px;

  .benefits__item2 {
    margin: 37px 0;
    flex-direction: row-reverse;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $color-white;
    border: 1px solid #E8E9ED;
    border-radius: 5px;
    min-height: 370px;
    @include tablet {
      flex-direction: column-reverse !important;
      align-items: center;
      padding: 0 20px !important;


    }

    &.benefits__item1 {
      padding: 35px 100px 35px 35px;

    }

    .item__text {
      .text__title {
        font-family: $raleway;
        font-size: 2.25rem;
        letter-spacing: 0;
        color: $color-black;
        line-height: 2.625rem;
      }

      .text__text {
        font-family: $openSans;
        font-size: 1rem;
        letter-spacing: 0;
        color: $color-black;
        line-height: 1.375rem;
      }
    }
  }

  &__item2 .item__text {
    width: 45%;
  }

  &__item3 {
    background-color: $color-light-grey;
    border: 0;

    .item__text,
    .item__imageBox {
      background-color: $color-white;
      height: 370px;
      border: 1px solid #E8E9ED;
      border-radius: 5px;

    }
  }

  .benefits__item2 {
    margin: 37px 0;
    flex-direction: row-reverse;
    @include tablet {
      margin: 15px;
    }
  }


  .benefits__item3 .item__text {
    justify-content: center;
    display: flex;
    flex-direction: column;
    margin-right: 60px;
    padding-left: 35px;
  }


  .bf__container {
    max-width: 1200px;
    box-sizing: border-box;
    @include tablet {
      padding: 0 15px;
      margin: 15px;
    }
  }

  .benefits__item1 .item__image {
    width: 100%;
    height: auto;
    max-width: 380px;
    max-height: 219px;
  }

  .benefits__item {
    .bf__item {
      position: relative;

      .item__image {
        position: relative;
        z-index: 3;
        @include tablet {
          width: 192px;
          margin-top: 30px;
        }
      }


      .text__text::after {
        content: "";
        display: block;
        height: 4px;
        background: $color-redish;
        width: 80%;
        position: absolute;
        bottom: 0;
        left: 0;
        max-width: 329px;
        @include tablet {
          display: none;
        }
      }
    }

    &.benefits__item1.bf__container > div:nth-child(2) {
      position: relative;
      width: 100%;
      min-height: 219px;
      height: auto;
      max-width: 380px;
      z-index: 0;
      @include tablet {
        width: 50%;
      }

      &::after {
        content: "";
        position: absolute;
        background: #FB3B64 0 0 no-repeat padding-box;
        border-radius: 41px;
        transform: matrix(0.71, 0.71, -0.71, 0.71, -165, -35);
        height: 180px;
        width: 180px;
        z-index: 1;
        @include tablet{
          transform: matrix(0.71, 0.71, -0.71, 0.71, -85, 7);
          height: 100px;
          width: 100px;
        }
      }
    }
  }


  .benefits__item.benefits__item2.bf__container > div.bf__item2 {
    position: relative;
    z-index: 2;

    &::after {
      content: "";
      position: absolute;
      background: $color-primary 0 0 no-repeat padding-box;
      height: 320px;
      width: 320px;
      border-radius: 50%;
      top: -15px;
      right: 120px;
      z-index: 1;
    }

    .item__image {
      position: relative;
      padding-left: 70px;
      z-index: 2;
    }
  }

  .benefits__item2 .item__image {
    width: auto;
    height: 328px;
    position: relative;
  }

  .benefits__item3 .item__image {
    width: auto;
    height: 208px;
  }

  .benefits__item3 .item__imageBox {
    background-color: $color-green;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .text__text {
    position: relative;
    padding-bottom: 30px;
    padding-right: 20px;

    &::after {
      content: "";
      display: block;
      height: 4px;
      background: $color-primary;
      width: 80%;
      position: absolute;
      bottom: 0;
      left: 0;
      max-width: 329px;
    }
  }
}